angular
  .module('app', ['ui.router', 'ui.bootstrap', 'ngMessages', 'angular-loading-bar', 'LocalStorageModule', 'toastr', 'ngAnimate', 'angular.chips','ngFileUpload'])
  .config(function ($httpProvider, $compileProvider) {
    $httpProvider.interceptors.push('AuthInterceptor');
    $httpProvider.useApplyAsync(true);
    $compileProvider.debugInfoEnabled(false);
  })
  .run(function ($transitions, $q) {
    var match = {
      to: function (state) {
        return state.data && state.data.roles;
      }
    };
    $transitions.onStart(match, function (trans) {
      var Auth = trans.injector().get('Auth');
      var state = trans.$to();
      return Auth.getCurrentUser()
        .then(function (user) {
          if(!user.hasOwnProperty('role') || state.data.roles.indexOf(user.role) === -1){
            return $q.reject('access error');
          }
        });
    });
    $transitions.onError(match, function (trans) {
      var $state = trans.router.stateService;
      $state.go('login');
    });
  });


(function () {
  angular
    .module('app')
    .component('createDeviceComponent', {
      templateUrl: 'app/components/device/device-list/create/device.create.component.html',
      bindings: {
        resolve: '<',
        close: '&',
        dismiss: '&'
      },
      controller: createDeviceComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function createDeviceComponent(Device, toastr) {
    var vm = this;
    vm.$onInit = function () {
      vm.device = vm.resolve.device;
    };
    vm.cancel = function () {
      vm.dismiss({$value: 'cancel'});
    };
    vm.save = function () {
      if(vm.device._id){
        Device.updateDevice(vm.device)
          .then(function () {
            vm.close({$value: vm.device});
          })
          .catch(function (error) {
            var message;
            if (error.data && error.data.message) {
              message = error.data.message;
            } else {
              message = 'Something went wrong';
            }
            toastr.error(message, 'Error');
          });
      } else {
        Device.createDevice(vm.device)
          .then(function (response) {
            vm.close({$value: response.data});
          })
          .catch(function (error) {
            var message;
            if (error.data && error.data.message) {
              message = error.data.message;
            } else {
              message = 'Something went wrong';
            }
            toastr.error(message, 'Error');
          });
      }
    }
  }
})();

